import Vue from "vue";
import wrap from '@vue/web-component-wrapper';
import SafaGridView from './components/SafaGridView';



const CustomElement = wrap(Vue, SafaGridView);
// The API for registering a web component is customElements.define()
// "custom element" = "web component" 
window.customElements.define('saf-grid-view', CustomElement);





Vue.config.productionTip = false;

new Vue({
    render: h => h(SafaGridView)
}).$mount("#saf-grid");